.App {
  font-family: sans-serif;
  background: red;
  text-shadow: 2px 2px rgba(0, 0, 0, 0.2);
  color: rgb(32, 151, 167);
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.Buttons {
  width: 40px;
}

.ClockController {
  position: fixed;
  
  margin: auto;
  top: 0px;
  width: 20px;
  height: 20px;
  z-index: 2003;
  font-size: 20px; 
  -webkit-font-feature-settings: "kern" off; 
          font-feature-settings: "kern" off; 
  -webkit-font-kerning: none; 
          font-kerning: none;
  right: 15px;
  
}
.TimeLabel {
  position: fixed;
  margin: auto;
  background:rgba(77, 91, 129, 0);
  top: 0px;
  width: 400px;
  height: 80px;
  z-index: 2000;
  font-size: 20px; 
  -webkit-font-feature-settings: "kern" off; 
          font-feature-settings: "kern" off; 
  -webkit-font-kerning: none; 
          font-kerning: none;
  left: 50%;
  margin-left: -200px;
}
.Fullscreen {
  position: fixed;
  top: 1px;
  width: 50px;
  z-index: 2000;
}
.TimeSwipe {
  position: fixed;
  bottom: 1px;
  background: rgb(77, 91, 129);
  width: 100%;
  z-index: 2001;
}
.TimeSelectorh {
  position: fixed;
  bottom: 0px;
  left: 50%;
  margin-left: -250px;
  width: 550px;
  height: 100px;
  z-index: 2001;
}
.TimeSelectorv {
  position: fixed;
  top: 50%;
  right: 0px;
  margin-top: -250px;
  width: 100px;
  height: 200px;
  z-index: 2001;
}

.Globe {
  position: fixed;
  background: black;
  top: 0;
  left:0px;
  height: 100%;
  width: 100%;
  z-index: 1;
}
.Miniglobe {
  position: fixed;
  box-shadow: 2px 2px 5px rgba(37, 38, 39, 0.397);
  background:  linear-gradient(rgb(67, 124, 199), #111) repeat scroll 0 0 #222;
  height: 100px;
  width: 200px;
  top: 20px;
  left: 20px;
  z-index: 2; 
}

.TimeContainer {
    position: relative;
    background: rgba(26, 60, 99, 0.274);
    overflow: hidden;
}

.TimeScale {
    position: relative;
    white-space: nowrap;
    -webkit-font-feature-settings: "kern" off;
            font-feature-settings: "kern" off;
    -webkit-font-kerning: none;
            font-kerning: none;
    background: rgba(26, 60, 99, 0);
    top: 0px;
    left: 0px;
}

.TimeScaleLabel {
    position: absolute;
    display: hidden;
    -webkit-font-feature-settings: "kern" off;
            font-feature-settings: "kern" off;
    -webkit-font-kerning: none;
            font-kerning: none;
    bottom: 3px;
    left: 50%;
    margin-left: -200px;
    width: 400px;
    height: 40px;
    background: rgba(165, 151, 63, 0.5);
}
